* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #ffffff;
  font-family: "Raleway", sans-serif;
  color: #1d1f22;
  font-size: 16px;
}

h1 {
  font-size: 42px;
}

h4 {
  font-weight: 500;
}

h2 {
  font-size: 30px;
  font-weight: normal;
}

h3 {
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
}

.container {
  max-width: 1240px;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

section.hidden .hide {
  display: none;
}

a {
  color: #1d1f22;
  text-decoration: none;
}

/* header */

#head {
  display: flex;
  justify-content: space-between;
  padding: 28px 0 0;
}

#head ul {
  display: flex;
  list-style-type: none;
}

#head ul li {
  margin-right: 32px;
  align-self: center;
}

#head ul a.active {
  color: #5ece7b;
}

#head ul a.active::after {
  background: #5ece7b none repeat scroll 0 0;
  bottom: -20px;
  content: "";
  height: 2px;
  left: -16px;
  margin: 0 auto;
  position: absolute;
  right: -16px;
  text-align: center;
}

#head ul a {
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
  font-weight: 600;
}

.custom-select-container {
  display: inline-block;
  min-width: 120px;
  text-align: center;
  position: relative;
}

.custom-select-container .selected-text {
  background-color: #fff;
  padding: 6px 25px;
  text-align: end;
  font-size: 18px;
  font-weight: 500;
}

.custom-select-container .selected-text::after {
  content: "";
  position: absolute;
  right: 10px;
  top: 16px;
  border: 7px solid transparent;
  border-color: #1d1f22 transparent transparent transparent;
}

.custom-select-container .selected-text.active::after {
  top: 8px;
  border-color: transparent transparent #1d1f22 transparent;
}

.custom-select-container ul {
  margin: 0;
  padding: 0;
  text-align: center;
}

.custom-select-container .select-options {
  position: absolute;
  left: 40px;
  width: 140px;
  padding: 15px 0;
  flex-direction: column;
  z-index: 100;
  box-shadow: 5px 10px 18px #8d8f9a;
}

.custom-select-container li {
  display: flex;
  list-style-type: none;
  padding: 10px 20px;
  background: #fff;
  cursor: pointer;
  margin-right: 0 !important;
  font-size: 18px;
  font-weight: 500;
  width: 100%;
}

.custom-select-container li:hover {
  background-color: #8d8f9a;
  color: #ffffff;
}

.nav-cart {
  position: relative;
}

.incart {
  position: absolute;
  top: -10px;
  left: 12px;
  width: 20px;
  color: #fff;
  background-color: #1d1f22;
  border-radius: 50%;
  text-align: center;
  font-weight: 600;
}

/* category page */

section .title {
  text-transform: capitalize;
  margin-bottom: 88px;
  margin-top: 80px;
}

.products {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.products article {
  margin-bottom: 120px;
  padding: 16px;
}

.product-image {
  display: block;
  width: 354px;
  height: 330px;
  background: no-repeat 50% 50% / contain;
}

article .no-stock-image,
article .cart-image {
  position: relative;
}

article .no-stock-image .product-image {
  z-index: 1;
  opacity: 0.4;
}

.no-stock {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  text-transform: uppercase;
  word-wrap: initial;
  font-size: 24px;
  color: #8d8f9a;
  white-space: nowrap;
}

.cart-item .green-cart {
  position: absolute;
  bottom: -35px;
  right: 0;
  z-index: 2;
}

.green-cart-hide {
  display: none;
}

.product-name {
  font-weight: 300;
  font-size: 18px;
  margin-bottom: 10px;
}

.product-price {
  font-weight: 500;
  font-size: 18px;
}

.product-details {
  margin-top: 24px;
}

article.cart-item {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.15);
}

/* cart box */

section.cart-on {
  position: relative;
  margin-top: -80px;
}
section.cart-on.hidden {
  position: relative;
  margin-top: 0;
}

section.cart-on .container {
  position: relative;
}

.cart-on #overlay {
  position: fixed;
  top: 80px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /* Black background with opacity */
  z-index: 2;
}

section.cart-on .cart-box {
  position: fixed;
  top: 80px;
  right: 0;
  bottom: 0;
  padding: 8px 16px 20px;
  min-width: 325px;
  z-index: 3;
  background-color: #ffffff;
  overflow-y: auto;
}

.cart-title {
  margin-bottom: 23px;
}

.cart-on .grid-container {
  display: grid;
  grid-column-gap: 15px;
  grid-row-gap: 5px;
  grid-template-columns: 155px 30px 105px;
  grid-template-rows: auto;
  margin-bottom: 40px;
  border-top: 1px solid #8d8f9a;
  padding-top: 15px;
}

.grid-item-img {
  grid-row-end: span 3;
  display: block;
}

.cart-box .grid-item-img {
  background: no-repeat 50% 50% / contain;
}

.grid-item7 {
  display: flex;
  align-items: flex-end;
}

.grid-item5,
.grid-item2 {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.grid-item2 button,
.grid-item7 button {
  background-color: #ffffff;
  border: 1px solid #1d1f22;
  font-size: 18px;
  width: 30px;
  padding: 5px;
}

.grid-item4,
.grid-item5 {
  font-weight: 500;
  font-weight: bold;
}

.grid-item7 {
  justify-content: center;
}

.grid-item6 button {
  margin: 5px;
  min-width: 30px;
  min-height: 30px;
}

.cart-box button {
  padding: 7px;
  background-color: #ffffff;
  border: 1px solid #1d1f22;
}

.cart-box .grid-item2 button,
.cart-box .grid-item7 button {
  background-color: #ffffff;
  border: 1px solid #1d1f22;
  font-size: 14px;
  width: 24px;
  padding: 5px;
}

.cart-box .grid-item2 button {
  align-items: flex-end;
}
.cart-box .grid-item7 button {
  align-items: flex-start;
}

.cart-box .total {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 12px;
}

.cart-box .total h4 {
  font-weight: bold;
}

.cart-box .box-action {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 35px;
}

.box-action button {
  width: 150px;
  padding: 13px;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 14px;
}

.box-action .bag {
  background-color: #ffffff;
  border: 1px solid #1d1f22;
}

.box-action .out {
  background-color: #5ece7b;
  border: 1px solid #5ece7b;
  color: #fff;
}

/* currency */

.currency {
  position: absolute;
  top: 0;
  right: 400px;
  padding: 5px;
  width: 60px;
  z-index: 3;
  background-color: #ffffff;
}

.currency div {
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 18px;
  width: 100px;
}

/* cart page */

.cart-page .grid-container {
  display: grid;
  grid-column-gap: 15px;
  grid-row-gap: 10px;
  margin-bottom: 40px;
  grid-template-rows: auto;
  grid-template-columns: repeat(3, minmax(150px, 1fr));
}

.cart-page .grid-item1 {
  font-size: 30px;
}

.cart-page .grid-item2,
.cart-page .grid-item5,
.cart-page .grid-item7 {
  justify-content: flex-end;
}

.cart-page .grid-item4,
.cart-page .grid-item5 {
  font-size: 24px;
  font-weight: 600;
}

.cart-page .grid-item5 {
  padding-right: 15px;
}

.cart-page .grid-item6 {
  display: flex;
  align-items: center;
}

.cart-page .grid-item6 h4,
.cart-page .grid-item6 div {
  margin-right: 10px;
}

.cart-page .grid-item6 button {
  padding: 6px;
  margin: 3px;
}
.cart-page .grid-item6 button.selected {
  padding: 2px;
}
.cart-box button.selected {
  padding: 3px;
}

.cart-page .grid-item-img {
  overflow: hidden;
  max-width: 150px;
  height: auto;
  display: flex;
  position: relative;
}

.cart-page .grid-item-img img {
  object-fit: cover;
  width: 100%;
  height: auto;
  display: block;
}

.cart-page .grid-item-img .carousel-show {
  display: block;
  object-fit: cover;
  width: 100%;
  height: auto;
}

.cart-page .grid-item-img .hidden {
  display: none;
}

.slide-next {
  font-size: 1.875rem;
  line-height: 2.25rem;
  cursor: pointer;
  position: absolute;
  right: 5px;
  top: 50%;
  bottom: 50%;
}

.slide-prev {
  font-size: 1.875rem;
  line-height: 2.25rem;
  cursor: pointer;
  position: absolute;
  left: 5px;
  top: 50%;
  bottom: 50%;
}

@media screen and (max-width: 768px) {
  .cart-page .grid-item1 {
    font-size: 22px;
  }
  .cart-page .grid-item4,
  .cart-page .grid-item5 {
    font-size: 20px;
  }

  .cart-page .grid-item6 {
    flex-wrap: wrap;
  }

  .cart-page button {
    font-size: 18px;
    padding: 5px;
    border-width: 1px;
  }

  .cart-page .grid-item5 {
    padding-right: 7px;
  }
}

/* product page */

.product-img {
  display: flex;
}

.product {
  display: grid;
  grid-template-columns: 3fr 1fr;
  column-gap: 80px;
}

.product {
  margin-top: 80px;
  padding: 0 20px;
  width: 90%;
}

.small-img {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.small-img div {
  margin: 0 33px 33px 0;
  display: block;
  width: 80px;
  height: 80px;
  background: no-repeat 50% 50% / contain;
}

.main-img {
  overflow: hidden;
  width: 100%;
  height: 510px;
  display: flex;
  position: relative;
}

.main-img .carousel-show {
  display: block;
  object-fit: cover;
  width: 100%;
  height: auto;
}

.main-img .hidden {
  display: none;
}
.main-img div {
  display: flex;
}

.small-img .img-show {
  border: 1px solid #000;
}

.product-info h1 {
  font-size: 30px;
  font-weight: normal;
  margin: 0 0 43px;
}

.product-info h4 {
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
}

.product-info button {
  padding: 13px;
}

.product-info button,
.cart-page .grid-item6 button {
  background-color: #ffffff;
  color: #1d1f22;
  border: 1px solid #1d1f22;
}

.product-info button:not(:last-child) {
  margin: 0 10px 10px 0;
}

.product-info button.selected.swatch,
.cart-page .grid-item6 button.selected.swatch,
.cart-box .grid-item6 button.selected.swatch {
  border-radius: 20px;
}

.product-info button.selected,
.cart-page .grid-item6 button.selected,
.cart-box .grid-item6 button.selected {
  color: #fff;
  border: 5px solid #1d1f22;
  background-color: #1d1f22;
}

.product-info button.selected {
  padding: 10px;
}

.nostock {
  color: #fff;
  border: 1px solid #1d1f22;
  background-color: #1d1f22;
  width: 100%;
  padding: 16px;
}

.product-info .add-cart {
  width: 100%;
  color: #fff;
  border: 1px solid #5ece7b;
  background-color: #5ece7b;
  padding: 16px;
  font-weight: 600;
}

@media screen and (max-width: 768px) {
  .product {
    grid-template-columns: 1fr;
    margin-top: 0;
  }

  h1 {
    display: none;
  }

  h2 {
    display: block !important;
  }
}

@media screen and (min-width: 768px) and (max-width: 992px) {
  .product {
    width: 100%;
    column-gap: 40px;
  }
}

@media screen and (max-width: 992px) {
  .product-img {
    flex-flow: column-reverse;
    align-self: flex-start;
  }

  .small-img {
    flex-direction: row;
    margin-top: 15px;
  }

  .small-img img {
    margin: 0 15px 15px 0;
  }
}
